  export default {
    name: 'docInfo',
    data () {
      return {
        selected:'1',
        value:false
      }
    },
    created() {
      document.getElementsByTagName('title')[0].innerHTML = '我的信息'
    },
    methods: {
      openPhone() {
         this.value = !this.value;
      }
   }
  }

