<style lang="scss">
  .doc-info-box{
    height:100vh;
    background: #f4f4f4;
    .infoHeader{
      height:30vh;
      width:100%;
      background-size:100% 100%;
      background:url(../../assets/img/docInfo.png);
      .headerBox{
        padding:30px 16px 0;
        display:flex;
        justify-content: space-between;
        .headerLeft{
          display:flex;
          justify-content: flex-start;
          .infoImg{
