          color:#fff;
          line-height:60px;
        }
      }
    }
    .info-container{
      position: relative;
      margin:-65px 16px;
      background:#fff;
      box-shadow: 0 12px 5px -2px #EBEFF0; /*底边阴影*/
      border-radius:5px;
      .info-box{
        .info-set{
          display:flex;
          justify-content: space-between;
          padding:15px 16px;
          border-bottom:1px dashed #f4f4f4;
