        border-radius: 14px 0 0 14px;
      }
      b{
        position: absolute;
        width: 7px;
        height: 14px;
        top: 40px;
        border: 1px solid #66B2AA;
        z-index: 33;
        background:#66B2AA;
        FILTER: alpha(opacity=0); /*androd*/
